@import url("https://fonts.googleapis.com/css2?family=Black+Han+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap");

.skillsContainer {
  width: 100%;

  padding-bottom: 80px;

  display: flex;
  justify-content: center;

  background-color: #585858;
}

.skillsContentBox {
  margin-top: 70px;

  /* width: 1140px; */
  width: 60%;
  max-width: 1140px;
  /* width 계속 고민해보기 줄였다 키웠다 ~ */

  display: flex;
  flex-direction: column;
  align-items: center;
}

.skillsTitleBox {
  font-family: "Black Han Sans", sans-serif;
  font-size: 50px;
  color: #ffffff;

  border-bottom: 1px solid #cccccc;

  margin-bottom: 15px;
}

.skillsDetailBox {
  width: 100%;

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 15px;
  justify-content: center;
}

.skillsSkillBox {
  width: 355px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  /* border-bottom: 1px solid #f0f0f0; */
  background-color: #ffe139;
  border-radius: 25px;
  padding: 10px 0;
}

.skillsskillTitleBox {
  font-family: "Noto Sans KR", sans-serif;
  color: #ffffff;
  font-size: 20px;
  font-weight: 800;
  border-bottom: 2px solid #ffffff;
  width: 200px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-bottom: 5px;
  margin-bottom: 10px;
}

.skillsskillTitleImgBox {
  width: 50px;
  height: 50px;

  margin-right: 15px;

  background-color: #ffffff;
  border-radius: 5px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.skillsskillTitleImgBox img {
  width: 80%;
  height: 80%;
  object-fit: cover; /* 이미지 비율 유지하며 크기에 맞추기 */
}

.skillsskillDetailBox {
  width: 95%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px;
}

.skillsskillDetailImgBox {
  width: 65px;
  height: 65px;
  border-radius: 10px;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;

  /* 여기 일단 팝업을 위한.. */
  position: relative;
}

.popupText {
  position: absolute;
  bottom: 110%; /* 아이콘 위로 띄우기 */
  left: 50%;
  transform: translateX(-50%);
  width: 200px;
  padding: 10px;
  background: rgba(0, 0, 0, 0.8); /* 반투명 배경 */
  color: #fff;
  font-size: 14px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.3s ease;
}

.skillsskillDetailImgBox:hover .popupText {
  visibility: visible;
  opacity: 1;
}

.skillsskillDetailImgBox img {
  width: 55px;
  height: 55px;
  transition: transform 0.3s ease;
  cursor: pointer;
}

.skillsskillDetailImgBox img:hover {
  transform: scale(1.1);
}

.popupText p {
  white-space: pre-line;
}

@media screen and (max-width: 450px) {
  .skillsContentBox {
    width: 100%;
  }

  .skillsTitleBox {
    font-size: 38px;
  }

  .skillsDetailBox {
    width: 90%;
  }
}
