@import url("https://fonts.googleapis.com/css2?family=Black+Han+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap");

.aboutContainer {
  width: 100%;
  display: flex;
  justify-content: center;

  padding-bottom: 80px;/* 다음 컴포넌트와의 거리차이*/
}

.aboutContentBox {
  margin-top: 70px;
  /* width: 1140px; */
  width: 60%;
  max-width: 1140px;
  /* width 계속 고민해보기 줄였다 키웠다 ~ */

  display: flex;
  flex-direction: column;
  align-items: center;
}

.aboutTitleBox {
  font-family: "Black Han Sans", sans-serif;
  font-size: 50px;

  border-bottom: 1px solid #cccccc;

  margin-bottom: 15px;
}

.aboutDetailBox {
  width: 100%;

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 15px;
  justify-content: center;
}

.aboutDetail {
  width: 350px;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  /* border-bottom: 1px solid #f0f0f0; */
  padding: 5px 0;
}

.aboutDetailImgBox {
  width: 50px;
  height: 50px;

  /* background-image: url("../../img/profile.png");
    background-size: cover;
    background-repeat: no-repeat; */
}

.aboutDetailContentBox {
  width: 140px;
  display: flex;
  flex-direction: column;
  margin-left: 35px;

  font-family: "Noto Sans KR", sans-serif;
}

.aboutDetailTitle {
  margin-bottom: 8px;
  font-size: large;
}


@media screen and (max-width: 450px) {

  .aboutTitleBox{
      font-size: 38px;
  }
}