@import url("https://fonts.googleapis.com/css2?family=Black+Han+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap");

.projectsContainer {
  width: 100%;

  padding-bottom: 80px;

  display: flex;
  justify-content: center;

  /* margin-bottom: 80px; */
  /*  다음 컴포넌트와의 거리차이 */
}

.projectsContentBox {
  margin-top: 70px;

  /* width: 1140px; */
  width: 60%;
  max-width: 1140px;
  /* min-width: 400px; */
  /* width 계속 고민해보기 줄였다 키웠다 ~ */

  display: flex;
  flex-direction: column;
  align-items: center;
}

.projectsTitleBox {
  font-family: "Black Han Sans", sans-serif;
  font-size: 50px;
  /* color: #FFFFFF; */

  border-bottom: 1px solid #cccccc;

  margin-bottom: 20px;
}

.projectsDetailBox {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: center;
}

.projectsProjectBox {
  width: 400px;
  height: 600px;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  /* align-items: center; */

  /* border-bottom: 1px solid #f0f0f0; */
  border: 1px solid orange;
  border-radius: 25px;
}
.projectsProjectTitleBox {
  width: 85%;
  padding: 10px 7.5%;
  margin-top: 5px;
  display: flex;
  flex-direction: column;

  border-bottom: 1px solid rgb(255, 234, 196);
}

.projectsProjectTitleBox > :first-child {
  margin-bottom: 10px;

  font-family: "Noto Sans KR", sans-serif;
  font-size: larger;
  font-weight: bolder;

  display: flex;
  flex-direction: row;

  justify-content: space-between;
  align-items: center;
}
.projectsProjectTitleBox > :nth-child(2) {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.projectsProjectTitleBox img {
  width: 25px;
  height: 25px;
}

.projectsProjectSkillBox {
  width: 90%;
  padding: 10px 5%;

  display: flex;
  flex-direction: row;

  border-bottom: 1px solid rgb(255, 234, 196);
}

.projectsProjectSkillBox > div {
  width: 30px;
  height: 30px;

  justify-content: center;
  align-items: center;

  margin-right: 10px;
}

.skillImg {
  width: 100%;
  height: 100%;
}

.projectsProjectContentBox {
  width: 90%;
  padding: 10px 5%;

  display: flex;
  flex-direction: column;
}

.projectsProjectContentBox ul {
  list-style: none; /* 기본 리스트 스타일 제거 */
  padding: 0 0;
}

.projectsProjectContentBox li {
  position: relative; /* 아이콘을 배치하기 위해 상대 위치 설정 */
  padding-left: 25px; /* 아이콘 공간 확보 */
  margin-bottom: 8px; /* 리스트 간의 간격 */
  font-size: 16px;
  color: #333;
}

.projectsProjectContentBox li::before {
  content: "✓"; /* 원하는 아이콘 또는 텍스트 추가 */
  position: absolute;
  left: 0;
  color: orange; /* 아이콘 색상 */
  font-size: 16px; /* 아이콘 크기 */
}

.projectsProjectContactBox {
  width: 85%;
  padding: 10px 7.5%;
  border-top: 1px solid rgb(255, 234, 196);

  margin-top: auto; /* 위쪽 여백 auto로 만들어 하단배치*/

  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  gap: 10px;
}

.projectsProjectContactBox > div {
  width: 25px;
  height: 25px;

  cursor: pointer;
}

.projectsProjectContactBox img {
  width: 100%;
  height: 100%;
}

.proejectsProjectRoleBox {
  width: 90%;
  padding: 10px 5%;
  border-bottom: 1px solid rgb(255, 234, 196);
}

@media screen and (max-width: 450px) {
  .projectsContentBox {
    width: 100%;
  }
  .projectsTitleBox {
    font-size: 38px;
  }
  .projectsDetailBox {
    width: 90%;
  }
}
