.projectsDetailBox {
  display: flex;
  flex-wrap: wrap;
  /* max-width: 1200px; */
  max-width: 100%;
  margin: auto;
  padding: 20px;
  gap: 15px;
  align-items: stretch; /* 높이 맞추기 */
  justify-content: center;
}

/* 슬라이더 컨테이너 */
.sliderContainer {
  max-height: 500px; /* 높이 제한 */
  width: 45%;
  min-width: 350px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.imageWrapper {
  display: flex;
  justify-content: center;
}

.projectImage {
  width: 100%;
  height: 100%;
  max-height: 500px; /* 설명과 높이 맞춤 */
  object-fit: contain;
  border-radius: 10px;
}

/* 내용 컨테이너 (스크롤 추가) */
.contentContainer {
  flex: 1;
  display: flex;
  flex-direction: column;
  width: 45%;
  min-width: 350px;
  gap: 15px;
  max-height: 500px; /* 이미지와 맞춤 */
  overflow-y: auto; /* 내부 스크롤 추가 */
  padding-right: 10px; /* 스크롤 바 공간 확보projectImage */
}

.projectTitle {
  font-size: 24px;
  font-weight: bold;
}

.projectPeriod {
  font-size: 16px;
  color: gray;
}

.projectOutline {
  font-size: 16px;
  font-weight: bold;
}

/* 소제목 */
.subTitle {
  font-size: 18px;
  font-weight: bold;
  margin-top: 10px;
}

/* 기술 스택 */
.skills {
  display: flex;
  gap: 10px;
}

.skillIcon {
  width: 30px;
  height: 30px;
}

/* 설명 리스트 */
.descriptionList {
  list-style: none;
  padding: 0;
}

.descriptionItem {
  font-size: 16px;
  margin-bottom: 5px;
}

/* 링크 */
.linkContainer {
  display: flex;
  gap: 10px;
}

.linkIcon {
  width: 30px;
  height: 30px;
}

/* 뒤로가기 버튼 스타일 */
.backButton {
  background-color: #ffd700;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  position: fixed; /* 화면에 고정 */
  bottom: 20px; /* 화면 하단에 위치 */
  left: 50%; /* 화면 가운데에 배치 */
  transform: translateX(-50%); /* 정확히 중앙에 위치 */
  z-index: 10; /* 다른 콘텐츠 위에 표시 */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* 버튼에 그림자 추가 */
}

.backButton:hover {
  background-color: #e1bf00;
}

@media screen and (max-width: 450px) {
  .projectsContentBox {
    width: 100%;
  }
  .sliderContainer {
    width: 90%;
  }
  .contentContainer {
    width: 90%;
  }
  .projectsTitleBox {
    font-size: 38px;
  }
  .projectsDetailBox {
    width: 90%;
  }
}
