.headerContainer {
  width: 100%;
  height: 70px;

  display: flex;
  flex-direction: row;
  justify-content: center;

  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;

  background-color: #ffffff;
  box-shadow: 0 2px 8px hsla(0, 0%, 80%, 0.8);

  font-family: "Noto Sans KR", sans-serif;
}

.headerLogoAndMenuBox {
  /* width: 1140px; */
  width: 60%;
  max-width: 1140px;
  /* min-width: 1140px; */
  /* width 계속 고민해보기 줄였다 키웠다 ~ */
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.headerLogoBox {
  display: flex;
  align-items: center;
}

.headerMenuBox {
  display: flex;
  align-items: center;
}

.headerMenuBox nav ul {
  display: flex;
  justify-content: center;
  list-style: none;
}

.headerMenuBox nav ul li {
  width: 90px;
  text-align: center;
  cursor: pointer;
}

.headerMenuBox nav ul li:last-child {
  margin-right: 0px;
}

.headerMenuBox nav ul li:hover {
  color: #ffd700;
}

.headerMenuBox nav ul li.active {
  color: #ffd700;
}

.headerLogo {
  font-weight: bolder;
  font-size: x-large;
}

/* 햄버거 버튼 스타일 */
.hamburgerButton {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;

  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 100;
}

/* 830px 이하에서 메뉴 숨김 */
@media (max-width: 830px) {
  .headerLogoAndMenuBox {
    width: 90%;
  }

  .headerMenuBox {
    display: none;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 70px;
    right: 0;
    width: 23%;
    background: rgba(255, 255, 255, 0.8);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);

    border-bottom-left-radius: 10px;
  }

  /* 햄버거 버튼을 눌렀을 때 메뉴 보이기 */
  .open {
    display: flex;
  }

  .hidden {
    display: none;
  }

  .headerMenuBox nav ul {
    flex-direction: column;
    width: 100%;
    text-align: center;
  }

  .headerMenuBox nav ul li {
    width: 100%;
    padding: 10px 0;
  }

  .headerMenuBox nav ul li a {
    font-size: 20px;
  }
}
