@import url('https://fonts.googleapis.com/css2?family=Black+Han+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap');

.homeContainer{
    width: 100%;
    display: flex;
    height: 550px;
    justify-content: center;

    background-image: url("../../img/banner.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    opacity: 0.83; 
    /* 다음 컴포넌트와의 거리차이*/
}

.homeContentBox{
        /* width: 1140px; */
        width: 60%;
        max-width: 1140px;
        /* width 계속 고민해보기 줄였다 키웠다 ~ */

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        color: #FFFFFF;
}

.homeTitleBox{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.homeTitleBox{
    font-family: 'Black Han Sans', sans-serif;
    font-size: 50px;
}

.homeTextBox{
    margin-top: 50px;

    font-family: 'Noto Sans KR', sans-serif;
    font-size: 21px;
}

.homeTextBox{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.homeTextBox div:first-child{
    margin-bottom: 10px;
}

@media screen and (max-width: 1506px) {
    .homeTitleBox {
      font-size: 48px;
    }
    .homeTextBox{
        font-size: 20px;
    }
  }

  @media screen and (max-width: 1446px) {
    .homeTitleBox {
      font-size: 38px;
    }
    .homeTextBox{
        font-size: 19px;
    }
  }
  
  @media screen and (max-width: 1148px) {
    .homeTitleBox {
      font-size: 30px;
    }
    .homeTextBox{
        font-size: 17px;
    }
  }

  @media screen and (max-width: 910px) {
    .homeTitleBox {
      font-size: 20px;
    }
    .homeTextBox{
        font-size: 14px;
    }
  }

  @media screen and (max-width: 613px) {
    .homeContentBox{
      width: 90%;
    }
    .homeTitleBox {
      font-size: 18px;
    }
    .homeTextBox{
        font-size: 14px;
    }
  }